<template>
  <div class="product_list">
    <div class="breadcrumb">
      <el-breadcrumb separator="/">
        <el-breadcrumb-item :to="{ path: '/' }">home</el-breadcrumb-item>
        <el-breadcrumb-item :to="{ path: '/platform' }"
          ></el-breadcrumb-item
        >
        <el-breadcrumb-item></el-breadcrumb-item>
      </el-breadcrumb>
    </div>
    <div class="product_main">
      <div class="steps">
        <div class="step" v-for="(item, index) of 6" :key="index">
          <img
            class="true_img"
            src="../../assets/img/done@2x.png"
            alt=""
            v-if="index < 2"
          />
          <div
            class="num"
            :class="[index == 2 ? 'shownum' : '', index < 2 ? 'lastnum' : '']"
            v-else
          >
            1
          </div>
          <div
            class="desc"
            :class="[index == 2 ? 'showdesc' : '', index < 2 ? 'hasdesc' : '']"
          >
            
          </div>
          <div class="line" v-if="index < 5"></div>
        </div>
      </div>
      <div class="explain">
        <div class="explain_row" v-for="(item, index) of 9" :key="index">
          <div class="explain_left"></div>
          <div class="explain_right">2432363476456</div>
        </div>
        <div class="explain_row">
          <div class="explain_left"></div>
          <div class="explain_right">
              : 110m; :
            330m; :
            :0.1mm;:5cm; 
            ,
          </div>
        </div>
      </div>
      <div class="update">
        <img src="../../assets/img/update@2x.png" alt="" />
        <span></span>
        <input
          class="input_up"
          type="file"
          id="file"
          multiple="multiple"
          @change="upload($event)"
        />
      </div>
      <div class="files" v-for="(item, index) of 2" :key="index">
        <img class="file_img" src="../../assets/img/update@2x.png" alt="" />
        <div class="file">
          <span>.docx</span>
          <img class="del_img" src="../../assets/img/del.png" alt="" />
        </div>
      </div>
      <div class="admit" @click="toNext()"></div>
    </div>
    <div class="bottom"></div>
  </div>
</template>
<script>
import axios from "axios";

export default {
  data() {
    return {};
  },
  methods: {
    toNext() {
      this.$router.push({
        path: "/platform/stepfour",
        query: {},
      });
    },
    upload(e) {
      console.log(e);
      let url = "/api/task/upload-doc.do";
      let files = e.target.files;
      const param = new FormData();
      param.append("file", files[0]);
      // axios.defaults.headers.post["Content-Type"] = "multipart/form-data";
      // axios.defaults.timeout = 10000;
      // axios.defaults.withCredentials = true;

      var instance = axios.create({
        baseURL: "",
        timeout: 10000,
        withCredentials: true,
        headers: { "Content-Type": "multipart/form-data" },
      });
      instance({
        method: "post",
        url: url,
        data: param,
      })
        // instance.post(url, param)
        .then((res) => {
          console.log(res.data);
        });
    },
  },
  created() {},
  components: {},
};
</script>
<style lang="scss" rel="stylesheet/scss" scoped>
.product_list {
  background: #f9f9f9;
}
.bottom {
  height: 24px;
}
.product_main {
  width: 1200px;
  background: #ffffff;
  margin: 0 auto;
  padding: 1px 0;
}
.steps {
  display: flex;
  justify-content: center;
  margin: 40px auto 40px;
  .step {
    display: flex;
    color: #aaaaaa;
    height: 32px;
    .num {
      width: 32px;
      height: 32px;
      border-radius: 16px;
      border: 1px solid #aaaaaa;
      text-align: center;
      line-height: 32px;
    }
    .true_img {
      width: 32px;
      height: 32px;
    }
    .desc {
      font-size: 16px;
      line-height: 32px;
      margin-left: 8px;
    }
    .line {
      width: 40px;
      height: 2px;
      background: #aaaaaa;
      margin: 0 16px;
      position: relative;
      top: 15px;
    }
    .shownum {
      background: #0695d4;
      color: #ffffff;
      border: 1px solid #0695d4;
    }
    .showdesc {
      font-weight: bold;
      color: rgba(0, 0, 0, 0.85);
    }
    .hasdesc {
      color: rgba(0, 0, 0, 0.65);
    }
  }
}
.admit {
  width: 98px;
  height: 32px;
  background: #0695d4;
  border-radius: 2px;
  font-size: 14px;
  color: #ffffff;
  text-align: center;
  line-height: 32px;
  margin: 24px 63px 36px 1039px;
  cursor: pointer;
}
.explain {
  width: 1071px;
  margin: 40px auto;
  background: #fafafa;
  border-radius: 2px;
  padding: 24px;
  .explain_row {
    display: flex;
    line-height: 22px;
    margin-top: 17px;
    .explain_left {
      width: 76px;
      text-align: right;
      font-size: 14px;
      color: rgba(0, 0, 0, 0.85);
    }
    .explain_right {
      font-size: 14px;
      color: rgba(0, 0, 0, 0.65);
      width: 722px;
    }
  }
}
.update {
  width: 106px;
  height: 32px;
  background: #ffffff;
  border: 1px solid rgba(0, 0, 0, 0.15);
  border-radius: 2px;
  font-size: 14px;
  color: rgba(0, 0, 0, 0.65);
  line-height: 32px;
  margin: 24px 0 0 87px;
  text-align: center;
  position: relative;
  img {
    width: 14px;
    height: 14px;
  }
  span {
    margin-left: 8px;
  }
  .input_up {
    opacity: 0;
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
  }
}
.files {
  height: 30px;
  line-height: 30px;
  margin-top: 14px;
  margin-left: 90px;
  display: flex;
  .file_img {
    height: 12px;
    width: 12px;
    position: relative;
    top: 9px;
  }
  .file {
    min-width: 255px;
    border-bottom: 2px solid rgba(0, 0, 0, 0.06);
    display: flex;
    justify-content: space-between;
    margin-left: 7px;
    span {
      font-size: 14px;
      color: #0695d4;
      cursor: pointer;
    }
    .del_img {
      width: 16px;
      height: 16px;
      position: relative;
      top: 7px;
      cursor: pointer;
    }
  }
}
</style>



